import React from 'react';
import "uikit/dist/css/uikit.min.css";
import "uikit/dist/js/uikit.min.js";
import "uikit/dist/js/uikit-icons.js";

class Questions extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            questions: [
                {
                    question: "what is the smallest indivisible part of an element",
                    answer: "atom",
                    option1: "element",
                    option2: "compound",
                    option3: "atom",
                    option4: "carbon",
                    id: 0
                },
                {
                    question: "what makes up a fish",
                    answer: "blood",
                    option1: "teeth",
                    option2: "bone",
                    option3: "blood",
                    option4: "car",
                    id: 1
                },
                {
                    question: "what is the smallest indivisible part of an element",
                    answer: "atom",
                    option1: "element",
                    option2: "compound",
                    option3: "atom",
                    option4: "carbon",
                    id: 2
                },
                {
                    question: "what makes up a fish",
                    answer: "blood",
                    option1: "teeth",
                    option2: "bone",
                    option3: "blood",
                    option4: "car",
                    id: 3
                }
            ],
            length: 4,
            answers: [{}],
            value: ""

        };
        //this.submitHandler = this.submitHandler.bind(this);
        this.changeHandler = this.changeHandler.bind(this);
    }

    submitHandler(id, e) {
        e.preventDefault();
        this.setState({
            answers: [...this.state.answers, { answer: this.state.value, id: id }]
        });
        console.log(`i ran successfully for ${this.state.value}`);
        this.setState({ value: "" })
        this.id === 3 ? this.id = 0 : this.id += 1;
        console.log(this.id)

    }

    componentDidMount() {

    }

    changeHandler(e) {
        this.setState({ value: e.target.value });
        console.log("change function ran successfully")
    }

    id = 0;
    render() {
        const temp = this.state.questions.filter(x => x.id === (this.id));
        return (
            <div style={{ minHeight: "100vh" }}>
                <h1>Questions</h1>
                {

                    <Form question={temp[0]} onChange={this.changeHandler} onSubmit={(e) => this.submitHandler(temp[0].id, e)} />
                }
            </div>
        );
    }

}

function Form(props) {
    return (
        <form className="uk-margin-small-left uk-margin-small-right" >
            <p>{props.question.id + 1}.    {props.question.question}</p>
            <input className="uk-margin-small" type="radio" name="options" id={props.question.option1} value={props.question.option1} onClick={props.onChange} />{props.question.option1}<br />
            <input className="uk-margin-small" type="radio" name="options" id={props.question.option2} value={props.question.option2} onClick={props.onChange} />{props.question.option2}<br />
            <input className="uk-margin-small" type="radio" name="options" id={props.question.option3} value={props.question.option3} onClick={props.onChange} />{props.question.option3}<br />
            <input className="uk-margin-small" type="radio" name="options" id={props.question.option4} value={props.question.option4} onClick={props.onChange} />{props.question.option4}<br />

            <input type="submit" value="Submit" className="uk-button uk-button-primary uk-width-1-1 uk-margin-small-bottom uk-position-bottom" onClick={props.onSubmit} />
        </form>
    );

}
export default Questions;